import { NavigateFunction } from "react-router-dom";

import RoundIconButton from "./RoundIconButton";
import Paths from "routing/Paths";

import { useNavigate } from "react-router-dom";
import { useCreateDebouncedCallback } from "hooks/useCreateDebouncedCallback";
import { store } from "components/AppProvider";

type GotoShop = (obj: { navigate: NavigateFunction; bookID: string }) => void;

const goToShop: GotoShop = ({ navigate, bookID }) => {
    if (bookID && typeof bookID === "string") {
        const path = "https://books.google.pl/books?id=" + bookID;

        try {
            window.open(path, "_blank");
        } catch (err: any) {
            let result = { error: true, errorMessage: err.message };
            store.dispatch({ type: "BOOKS_FETCH", payload: result });
            navigate(Paths.error);
        }
    }
};

interface Props {
    bookID: string;
}
const GoToShopButton = (props: Props) => {
    const { bookID } = props;
    const navigate = useNavigate();
    const handleClick = useCreateDebouncedCallback(goToShop, { navigate, bookID });
    return <RoundIconButton type="goToShop" ID={bookID} clickHandler={handleClick} />;
};

export default GoToShopButton;
