import debounce from "lodash/debounce";

import { useCallback } from "react";
import { useNavigate } from "react-router-dom";

import RoundIconButton from "./RoundIconButton";
import Paths from "routing/Paths";

import { useDispatchAction } from "hooks";
import { GOOGLE_API } from "config";

interface Props {
    bookID: string;
}
export const ShowBookDetailsButton = (props: Props) => {
    const { bookID } = props;
    const navigate = useNavigate();
    const { fetchDetails } = useDispatchAction();

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const getBookDetails = useCallback(
        debounce(() => {
            if (bookID) {
                const path = GOOGLE_API + bookID;
                fetchDetails(path);
                navigate(Paths.details);
            }
        }, 200),
        [bookID]
    );

    return <RoundIconButton type="showFullInfo" ID={bookID} clickHandler={getBookDetails} />;
};

export default ShowBookDetailsButton;
