import { ThunkAction } from "redux-thunk";
import { AnyAction } from "redux";

import { GOOGLE_API } from "config";
import { showError } from "../actionCreators";
import { RootStateType, FavoriteBooks } from "types";
import { NavigateFunction } from "react-router-dom";
import Paths from "routing/Paths";

export interface ThunkAddBookToFavoritesArgs {
    bookID: string;
    favorites: FavoriteBooks;
    navigate: NavigateFunction;
    showMessage: any;
}

export const thunkAddBookToFavorites = ({ bookID, favorites, navigate, showMessage }: ThunkAddBookToFavoritesArgs): ThunkAction<void, RootStateType, unknown, AnyAction> => {
    return async (dispatch, getState) => {
        const path = GOOGLE_API + bookID;

        fetch(path)
            .then(res => res.json())
            .then(json => {
                if (json) {
                    try {
                        favorites.add(bookID, json);
                        showMessage.success("Poprawnie dodano do ulubionych następującą książkę: " + json.volumeInfo.title);
                    } catch (error) {
                        dispatch(
                            showError({
                                isError: true,
                                errorMessage: "An attempt to add item to local storage caused error",
                            })
                        );
                        navigate(Paths.error);
                    }
                } else {
                    dispatch(
                        showError({
                            isError: true,
                            errorMessage: `Error when fetching data of book with ID = ${bookID}`,
                        })
                    );
                    navigate(Paths.error);
                }
            })
            .catch(error => {
                const result = {
                    isError: true,
                    errorMessage: error.message,
                };
                dispatch(showError(result));
                navigate(Paths.error);
            });
    };
};
